<template>
  <div class="container-fluid">
    <div class="row mt-5">
      <div class="col">
        <p class="col-12 title-section">
          Estadisticas de Uso Chatbot por Intenciones
        </p>
        <!-- <p class="col-12">Últimos 30 días</p> -->
      </div>
    </div>
    <div class="row mt-2">
      <div class="col-4">
        <label for="dateIniPicker"><b>Desde</b></label>
        <b-form-datepicker
          id="dateIniPicker"
          v-model="dateStart"
          class="mb-2"
        ></b-form-datepicker>
      </div>
      <div class="col-4">
        <label for="dateEndPicker"><b>Hasta</b></label>
        <b-form-datepicker
          id="dateEndPicker"
          v-model="dateEnd"
          class="mb-2"
        ></b-form-datepicker>
      </div>
    </div>
    <div class="row mt-2">
      <div class="col">
        <button
          class="btn btn-dark btn-secondary"
          @click="consultar()"
          style="background: #337bbe"
        >
          <span
            v-if="this.isLoading"
            class="spinner-border spinner-border-sm"
            role="status"
            aria-hidden="true"
          ></span>
          Consultar
        </button>
      </div>
    </div>
    <div class="row mt-5">
      <div class="col-12">
        <div class="card card-barchart">
          <!-- <div
            v-if="this.isLoading"
            class="d-flex justify-content-center spinner-loading"
          >
            <div
              class="spinner-border spinner-border-lg"
              role="status"
              style="width: 5rem; height: 5rem"
            >
              <span class="sr-only">Loading...</span>
            </div>
          </div> -->
          <bar-chart class="bar-chart" ref="barChart"></bar-chart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from "../store/index";
import moment from "moment";
import BarChart from "../components/BarChart";
import { getAbrevMonth, dame_color_aleatorio } from "../libs/funciones";
export default {
  name: "Home",
  components: { BarChart },
  data: () => {
    return {
      dateStart: "",
      dateEnd: "",
      typeQuery: "Diario",
      chartData: {},
      chartOptions: {},
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.loadingData;
    },
  },
  created() {
    this.$store.commit("SET_LAYOUT", "principal-layout");
  },
  mounted() {
    this.dateStart = moment()
      .subtract(1, "d")
      .format("YYYY-MM-DD");
    this.dateEnd = moment().format("YYYY-MM-DD");
    this.consultar();
  },
  methods: {
    //Desde el botón se consulta el tipo de reporte
    async consultar() {
      if (this.dateStart > this.dateEnd) {
        this.$toastr.e("Rango de fechas no válido.");
        return;
      }
      this.$store.commit("SET_LOADING_DATA", true);
      var report = [];
      report = await store.dispatch("getReportIntents", {
        dateStart: this.dateStart,
        dateEnd: this.dateEnd,
      });
      this.graficar(report);
    },
    // Grafica los datos
    graficar(report_info) {
      const report_labels = report_info.map((item) => {
        return item.intent;
      });
      const report_data = report_info.map((item) => {
        return item.count;
      });

      const colors = report_info.map((item) => {
        return dame_color_aleatorio();
      });

      var maxValue = Math.max(...report_data);

      this.chartData = {
        labels: report_labels,
        datasets: [
          {
            // label: "Enviados",
            data: report_data,
            backgroundColor: colors,
            // borderColor: "#1ab187",
            borderWidth: 1,
            barThickness: 25,
          },
        ],
      };
      this.chartOptions = {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
        layout: {
          padding: {
            left: 20,
            right: 20,
            top: 50,
            bottom: 20,
          },
        },
        scales: {
          yAxes: [
            {
              ticks: {
                suggestedMin: 0,
                suggestedMax: maxValue * 1.1,
                beginAtZero: true,
              },
            },
          ],
        },
      };
      if (this.$refs.barChart) {
        this.$refs.barChart.render(this.chartData, this.chartOptions);
      }
    },
  },
};
</script>
